import type { ChildProcess, SpawnOptions } from "node:child_process";
import { existsSync, readFileSync, writeFileSync } from "node:fs";
import { join } from "node:path";
import detectPackageManager from "which-pm-runs";
import spawn from "cross-spawn";
import * as Sentry from "@sentry/node";

export function installDeps(
  pkgMgr: string,
  cwd: string,
  depNames: string[],
  isDevDependency: boolean = false
) {
  const args: string[] = [];

  if (pkgMgr === "yarn") {
    args.push("add");
    if (isDevDependency) {
      args.push("--dev");
    }
  } else {
    args.push("install");
    if (isDevDependency) {
      args.push("--save-dev");
    }
  }

  depNames.forEach((depName) => {
    args.push(depName);
  });

  return runCommand(pkgMgr, args, {
    stdio: "ignore",
    env: {
      ...process.env,
      ADBLOCK: "1",
      DISABLE_OPENCOLLECTIVE: "1",
      // set NODE_ENV to development cuz
      // pnpm skips dev deps when in production
      NODE_ENV: "development",
    },
    cwd,
  });
}

export function manualPackageDepUpdates(
  rootDir: string,
  deps: string[],
  isDevDependency: boolean = false
) {
  const pkgJsonPath = join(rootDir, "package.json");
  const pkgJson = JSON.parse(readFileSync(pkgJsonPath, "utf8"));

  const targetDeps = isDevDependency ? "devDependencies" : "dependencies";

  pkgJson[targetDeps] = pkgJson[targetDeps] || {};

  deps.forEach((dep) => {
    pkgJson[targetDeps][dep] = "latest";
  });
  writeFileSync(pkgJsonPath, JSON.stringify(pkgJson, null, 2) + "\n");
}

export function runCommand(
  cmd: string,
  args: string[],
  options?: SpawnOptions
) {
  let child: ChildProcess;

  const command = new Promise<boolean>((resolve) => {
    try {
      child = spawn(cmd, args, {
        stdio: "inherit",
        ...options,
      });

      child.on("error", (e) => {
        Sentry.captureException(e);
        resolve(false);
      });

      child.on("close", (code) => {
        if (code === 0) {
          resolve(true);
        } else {
          Sentry.captureMessage(
            `cmd "${cmd} ${args.join(" ")}", exit code: ${code}`,
            "error"
          );
          resolve(false);
        }
      });
    } catch (e: any) {
      Sentry.captureException(e);
      resolve(false);
    }
  });

  const abort = async () => {
    if (child) {
      child.kill("SIGINT");
    }
  };

  return { abort, command };
}

export function getPackageManager(cwd: string) {
  try {
    if (existsSync(join(cwd, "pnpm-lock.yaml"))) {
      return "pnpm";
    }
    if (existsSync(join(cwd, "yarn.lock"))) {
      return "yarn";
    }
    if (existsSync(join(cwd, "package-lock.json"))) {
      return "npm";
    }
  } catch (e) {
    Sentry.captureException(e);
  }

  try {
    const detected = detectPackageManager();
    if (detected?.name) {
      return detected.name;
    }
  } catch (e) {
    Sentry.captureException(e);
  }

  return "npm";
}

export function getRunCommand(pkgManager: string) {
  if (pkgManager === "pnpm" || pkgManager === "yarn") {
    return pkgManager;
  }
  return `npm run`;
}
