interface UndoState {
  files: { path: string; content: string | null }[];
}

type Importance = 3 | 2 | 1;

interface ProjectFile {
  filePath: string;
  content: string;
  importance: Importance;
}

interface CodeGenCompletionBody {
  url?: string;
  files: ProjectFile[];
  hiddenFiles: string[];
  sessionId: string;
  customInstructions: CustomInstruction[];
  history: { role: "user" | "assistant"; content: string }[];
  mode: "exact" | "creative";
  userPrompt: string;
  reranker: number | undefined;
  prevId: string | undefined;
  maxTokens?: number;
  nextPage?: boolean;
}

interface CodeGenSearchBody {
  repoInfo: RepoInfo;
  query: string;
  limit: number;
}

interface RankedResult {
  filePath: string;
  startIndex: number;
  endIndex: number;
  score: number;
  id: string;
}

export interface CustomInstruction {
  id: string;
  name: string;
  content: string;
}

interface SessionContext {
  sessionId: string;
  history: { role: "user" | "assistant"; content: string; id?: string }[];
  undoStates: UndoState[];
  selectedFilePaths: Map<string, Importance>;
  hasNextPage?: boolean;
}

const PAGES_THRESHOLD = 5;

type CodegenUsage = {
  total: number;
  fast: number;
  quality: number;
  limits: {
    aiGeneration: number;
    aiGenerationContextWindow: number;
  };
};
export interface ArtifactItem {
  type: "shell" | "file" | "text" | "delta" | "done" | "diff";
  id?: string;
  content: string;
  filePath?: string;
  artifactTitle?: string;
  actionTitle?: string;
  synthetic?: boolean;
  incomplete?: boolean;
  stopReason?: "end" | "limit" | "error" | "max_tokens";
  usage?: CodegenUsage;
}

export const runCodeCommand = async (
  sys: DevToolsSys,
  subCommand: string,
  args: CLIArgs
) => {
  if (subCommand === "indexing") {
    await runCodeIndexing(sys, args);
  } else if (
    subCommand === "generate" ||
    subCommand === "gen" ||
    subCommand === "g"
  ) {
    await runCodeGen(sys, args);
  } else if (subCommand === "gen") {
    printHelp("code");
  } else {
    await runCodeGen(sys, args);
  }
};

export const runCodeGen = async (sys: DevToolsSys, args: CLIArgs) => {
  console.log("");
  intro(bgCyan(bold(white(" code "))) + `  ` + dim(VERSION));

  const credentials = await getCredentials(sys, args, {
    forceSpaceId: args.spaceId,
    builderPrivateKey: true,
    builderPublicKey: true,
  });

  const url = args.url;

  const ctx: SessionContext = {
    history: [],
    undoStates: [],
    sessionId: crypto.randomUUID(),
    selectedFilePaths: new Map(),
  };
  track("figma code started", {
    spaceId: credentials.builderPublicKey,
  });

  // New: Handle non-interactive mode
  if (args.prompt) {
    const mode = args.mode || "exact";
    const answer = { userPrompt: args.prompt, mode };
    await generateCompletion(
      sys,
      credentials,
      ctx,
      answer.userPrompt,
      answer.mode,
      url
    );
    return;
  }

  // Existing interactive mode
  while (true) {
    const answer = await askInteractiveQuestions(sys, credentials, ctx, !!url);
    if (answer === "exit") {
      break;
    }
    if (answer === "undo") {
      continue;
    }
    const userPrompt = answer.userPrompt;
    const mode = answer.mode;

    await generateCompletion(sys, credentials, ctx, userPrompt, mode, url);
  }
};

async function askInteractiveQuestions(
  sys: DevToolsSys,
  credentials: Credentials,
  sessionContext: SessionContext,
  hasDesign: boolean
) {
  const appRootDir = sys.getAppRootDir();
  let userPrompt = "";
  let mode: "exact" | "creative" | undefined;
  if (sessionContext.history.length === 0) {
    const placeholder = hasDesign
      ? `some examples: "add a header to the layout of the app", "add a new contact page", "update the card component"`
      : `some examples: "add a new page", "add a new component", "update the button component"`;

    const message = hasDesign
      ? "How does the design integrate into the project? (be detailed)"
      : "What do you want to do?";

    const integration = await text({
      message,
      placeholder: placeholder,
      validate(value) {
        if (hasDesign) {
          if (value.length === 0) {
            return "please, enter a detailed instruction";
          }
          if (value.length < 10) {
            return "a more detailed instruction is needed";
          }
        } else if (value.length === 0) {
          return "please, provide some prompt";
        }
      },
    });
    if (isCancel(integration)) {
      cancel("See you later! 👋");
      printFeedbackText(sessionContext.sessionId, credentials.builderPublicKey);
      return "exit";
    }
    userPrompt =
      integration ??
      "integrate design into the app, might be a new page/component/layout depending on the design";

    if (hasDesign) {
      const integrationMode =
        mode ??
        (await select({
          message: "How do you want to apply the changes?",
          options: [
            {
              value: "exact",
              label: "Precise",
              hint: "imports exactly as designed, might include artifacts",
            } as const,
            {
              value: "creative",
              label: "Match-my-style",
              hint: "design might be slighly changed to match the codebase, but looks better",
            } as const,
          ],
        }));
      if (isCancel(integrationMode)) {
        cancel("Command cancelled");
        return "exit";
      }
      mode = integrationMode;
    } else {
      mode = "creative";
    }
  } else {
    const lastCompletionId =
      sessionContext.history[sessionContext.history.length - 1]?.id;
    log.message(
      `${green("Changes are applied.")} What do you want to do next?\n
${bgBlack(" Ctrl+C ")} ${dim("to accept changes and exit")}
${bgBlack(` "undo" `)} ${dim("to undo last changes and try again")}`,
      { symbol: green("✔") }
    );

    const integration = await text({
      message: `${bgBlack(" Follow up ")} with more instructions:`,
      placeholder: ` Reply "accept", "undo", or provide follow-up feedback (Ctrl+C to exit)`,
      validate(value) {
        if (!value) {
          return "Please enter a value (Ctrl+C & exit)";
        }
      },
    });

    const isAccepted =
      isCancel(integration) || integration.trim().toLowerCase() === "accept";
    if (isAccepted) {
      outro(
        `${bgGreen(bold(white(" Done! 🎉 ")))}  ${dim(
          "session " + lastCompletionId
        )}`
      );
      printFeedbackText(sessionContext.sessionId, credentials.builderPublicKey);
      track("figma code accepted", {
        spaceId: credentials.builderPublicKey,
        userPrompt: userPrompt,
      });
      if (lastCompletionId) {
        await completionFeedback(
          sys,
          credentials,
          lastCompletionId,
          "positive"
        );
      }
      return "exit";
    }
    if (integration.trim().toLowerCase() === "undo") {
      if (lastCompletionId) {
        await completionFeedback(
          sys,
          credentials,
          lastCompletionId,
          "negative"
        );
      }
      sessionContext.history.pop(); // pop last assistant message
      sessionContext.history.pop(); // pop last user message
      const undoState = sessionContext.undoStates.pop();
      if (undoState) {
        for (const file of undoState.files) {
          if (file.content) {
            await sys.writeFile(sys.join(appRootDir, file.path), file.content);
          } else {
            await sys.unlink(sys.join(appRootDir, file.path));
          }
        }
        log.warn(
          `Restored files to previous state:\n${undoState.files
            .map((file) => dim(" - " + file.path))
            .join("\n")}\n`
        );
      }
      return "undo";
    }
    userPrompt = integration;
  }
  return { userPrompt, mode };
}

const SUGGEST_INDEXING = false;
const RERANKER_FALLBACK = 10;
const MAXFILES = 50;

async function generateCompletion(
  sys: DevToolsSys,
  credentials: Credentials,
  sessionContext: SessionContext,
  userPrompt: string,
  mode: "exact" | "creative" | undefined,
  url: string | undefined
): Promise<void> {
  const appRootDir = sys.getAppRootDir();
  const repoInfo = await sys.getRepoInfo();
  const packageJson =
    (await findPackageJson(sys, appRootDir)) && !repoInfo.remoteUrl;
  if (!packageJson) {
    const shouldInstall = await confirm({
      message:
        "Looks like this is an empty project. Would you like to set up a new Builder.io project?",
      active: "Yes, create new project",
      inactive: "Skip for now",
    });
    if (shouldInstall) {
      const passed = await runCommand(
        "npm",
        ["init", "builder.io@latest", "--", "--skip-question"],
        {
          stdio: "inherit",
          cwd: appRootDir,
        }
      ).command;
      if (!passed) {
        cancel(
          `Project creation failed. To create a new project manually, run:\n${bold(
            "npm init builder.io@latest"
          )}`
        );
        await exit(1);
      }
    }
  }

  const highlight = createEmphasize(common);

  let s: ReturnType<typeof spinner> | undefined = spinner();
  s.start(`Analyzing relevant files`);

  try {
    let files: string[] = [];
    let hiddenFiles: string[] = [];
    let currentChunks = false;
    let responded = false;

    let reranker: number | undefined;
    for (let i = 0; i < 2; i++) {
      const searchResults = await searchCodeBase(
        credentials.userId!,
        credentials.builderPublicKey!,
        credentials.builderPrivateKey!,
        {
          query: userPrompt,
          repoInfo,
          limit: RERANKER_FALLBACK,
        }
      );
      if (i === 0 && searchResults.length === 0) {
        if (SUGGEST_INDEXING) {
          if (s) {
            s.stop(
              "Codebase index not found. Indexing allows the CLI to automatically find relevant files."
            );
            s = undefined;
          }
          const confirmIndex = await confirm({
            message: "Do you want to index the codebase?",
            active: "Yes",
            inactive: "Skip",
          });
          if (confirmIndex === true) {
            await codeIndexing(sys, credentials);
            continue;
          }
        }
      }

      const allFiles = await getAllProjectFiles(appRootDir);

      if (searchResults.length > 0) {
        for (const file of allFiles) {
          const importance = shouldIncludeFile(
            file,
            searchResults,
            sessionContext.selectedFilePaths,
            0
          );
          if (importance !== 0 && files.length < MAXFILES) {
            files.push(file);
            sessionContext.selectedFilePaths.set(file, importance);
          } else if (shouldIncludeHiddenFile(sys, file)) {
            hiddenFiles.push(file);
          }
        }

        if (s) {
          s.stop(
            `Found relevant files of the project:\n${dim("│")}\n${files
              .map((a) => dim("│    " + a))
              .join("\n")}`
          );
          s = undefined;
        } else {
          log.info(
            `Found relevant files of the project:\n\n${files
              .map((a) => dim(a))
              .join("\n")}`
          );
        }
        reranker = undefined;
      } else {
        const presampledFiles = allFiles.filter((a) => {
          const importance = shouldIncludeFile(
            a,
            searchResults,
            sessionContext.selectedFilePaths,
            1
          );
          return importance === 1 || importance === 2;
        });
        reranker = 10;
        const includeFiles = Math.min(40, presampledFiles.length);
        const sampledFiles: string[] = [];
        let i = 0;
        while (
          sampledFiles.length < includeFiles &&
          i < presampledFiles.length * 2
        ) {
          i++;
          const file =
            presampledFiles[Math.floor(Math.random() * presampledFiles.length)];
          if (sampledFiles.includes(file)) {
            continue;
          }
          sampledFiles.push(file);
        }

        for (const file of allFiles) {
          const importance = shouldIncludeFile(
            file,
            sampledFiles,
            sessionContext.selectedFilePaths,
            1
          );
          if (importance !== 0 && files.length < MAXFILES) {
            files.push(file);
            sessionContext.selectedFilePaths.set(file, importance);
          } else if (shouldIncludeHiddenFile(sys, file)) {
            hiddenFiles.push(file);
          }
        }
      }
      break;
    }

    if (s) {
      s.stop(undefined);
      s = undefined;
    }

    const customInstructions = await getCustomInstructions(sys, appRootDir);
    s = spinner();
    s.start(`Thinking`);
    sessionContext.undoStates.push({
      files: [],
    });

    let streamable = true;
    let page = 0;
    const artifacts: ArtifactItem[] = [];
    while (streamable) {
      if (page > 0 && page % PAGES_THRESHOLD === 0) {
        log.warn(
          `Large generation in progress: ${page} pages generated so far.`
        );
        const continueGen = await confirm({
          message: "Do you want to continue generating?",
          active: "Continue",
          inactive: "Stop",
        });
        if (!continueGen) {
          log.info("Generation stopped by user");
          break;
        }
      }
      const body: CodeGenCompletionBody = {
        url,
        files: files.map((file) => ({
          filePath: file,
          content: sys.readFileSync(sys.join(appRootDir, file))!,
          importance: sessionContext.selectedFilePaths.get(file) ?? 1,
        })),
        customInstructions,
        hiddenFiles: hiddenFiles.slice(0, 300),
        sessionId: sessionContext.sessionId,
        history: sessionContext.history,
        mode: mode as "exact" | "creative",
        userPrompt: userPrompt,
        reranker,
        prevId: sessionContext.history[sessionContext.history.length - 1]?.id,
        // This setting help in testing pagination
        // maxTokens: 300,
        nextPage: Boolean(sessionContext.hasNextPage),
      };

      const stream = completionStream(credentials, body);

      for await (const streamItem of stream) {
        try {
          async function openedAction(json: ArtifactItem) {
            sessionContext.hasNextPage = false;
            if (currentChunks) {
              process.stdout.write(`\n`);
              currentChunks = false;
            }

            if (json.type === "file" && !json.synthetic) {
              s = spinner();
              s.start(`${json.actionTitle ?? json.artifactTitle}`);
            }
            if (json.type === "diff") {
              s = spinner();
              s.start(`${json.actionTitle ?? json.artifactTitle}`);
            }
            if (json.type === "done") {
              sessionContext.history.push({
                role: "user",
                content: userPrompt,
              });
              sessionContext.history.push({
                role: "assistant",
                content: json.content,
                id: json.id,
              });

              if (json.stopReason === "limit") {
                log.error(
                  red(
                    "🚨 The request was cancelled because it reached the account limits."
                  )
                );
                const url = new URL("https://builder.io/account/subscription");
                url.searchParams.set("space_id", credentials.builderPublicKey!);
                const r = await confirm({
                  message: dim(url.href),
                  active: "Open subscription page",
                  inactive: "Exit",
                });
                if (r) {
                  const { default: open } = await import("open");
                  await open(url.href);
                }
                await exit(1);
                return;
              }
              if (json.stopReason === "max_tokens") {
                sessionContext.hasNextPage = true;
              }

              if (json.usage) {
                log.info(
                  formatUsage(json.usage.total, json.usage.limits.aiGeneration)
                );
              }
            }
          }

          async function closedAction(json: ArtifactItem) {
            if (json.type === "diff") {
              if (s) {
                s.stop(
                  `${json.actionTitle ?? json.artifactTitle}: ${dim(
                    json.filePath
                  )}`
                );
                s = undefined;
              }
            }
            if (json.type === "file" && json.filePath) {
              const filePath = sys.join(appRootDir, json.filePath);
              const isUpdate = sys.existsSync(filePath);
              const oldContent = isUpdate ? sys.readFileSync(filePath) : null;
              sessionContext.undoStates[
                sessionContext.undoStates.length - 1
              ].files.push({
                path: json.filePath,
                content: oldContent!,
              });
              const formatted = await prettierFormat(
                json.content,
                undefined,
                filePath
              );
              await sys.writeFile(filePath, formatted);
              sessionContext.selectedFilePaths.set(json.filePath, 3);

              if (s && !json.synthetic) {
                s.stop(
                  `${
                    json.actionTitle ?? json.artifactTitle ?? "Created"
                  }: ${dim(json.filePath)}`
                );
                s = undefined;
              }
            }
            if (json.type === "shell") {
              log.message(
                `Requested to run command:\n${bgBlack(
                  " " + json.content.trim() + " "
                )}`,
                {
                  symbol: magenta("~"),
                }
              );
              const r = await confirm({
                message: "Do you want to execute and continue?",
                active: `Execute ${dim("(recommended)")}`,
                inactive: "Skip",
              });
              if (isCancel(r) || !r) {
                log.warn(
                  `Command skipped. Run it manually:\n${bgBlack(
                    " " + json.content + " "
                  )}.`
                );
                return;
              }
              s = spinner();
              s.start(`Running \`${json.content}\``);
              const result = await runShell(json.content, {
                shell: true,
                stdio: "inherit",
              });
              if (result) {
                s.stop(`Command \`${json.content}\` completed`);
              } else {
                s.stop(`Command \`${json.content}\` failed`, 2);
              }
              s = undefined;
            }
          }

          function deltaAction(json: ArtifactItem, delta: string) {
            if (json.type === "text") {
              if (!currentChunks) {
                process.stdout.write(dim("│  \n") + magenta("~  "));
                currentChunks = true;
              }
              process.stdout.write(delta.replace(/\n/g, "\n" + magenta("│  ")));
            }
            if (
              !json.synthetic &&
              (json.type === "file" || json.type === "diff")
            ) {
              if (s) {
                s.extra(
                  `\n\n${highlight
                    .highlightAuto(json.content)
                    .value.split("\n")
                    .slice(-10)
                    .map((a) =>
                      a.length > process.stdout.columns
                        ? a.slice(0, process.stdout.columns - 5)
                        : a
                    )
                    .join("\n")}`
                );
              }
            }
          }

          if (s && !responded) {
            s.stop("💡");
            s = undefined;
            responded = true;
          }

          if (streamItem.type === "delta") {
            const delta = streamItem.content as string;
            const lastArtifact = artifacts[artifacts.length - 1];
            if (lastArtifact) {
              lastArtifact.content += delta;
            }
            await deltaAction(lastArtifact, delta);
          } else {
            const lastArtifact = artifacts[artifacts.length - 1];
            if (lastArtifact) {
              lastArtifact.incomplete = false;
              await closedAction(lastArtifact);
            }
            artifacts.push(streamItem);
            await openedAction(streamItem);
          }
          if (streamItem.type === "done") {
            break;
          }
        } catch {}
      }
      streamable = Boolean(sessionContext.hasNextPage);
      page++;
    }
    if (currentChunks) {
      process.stdout.write(`\n`);
      currentChunks = false;
    }
  } finally {
    if (s) {
      s.stop();
      s = undefined;
    }
  }
}

async function* completionStream(
  credentials: Credentials,
  body: CodeGenCompletionBody
) {
  const url = new URL(`${apiAirV2Host}/codegen/completion`);
  url.searchParams.set("apiKey", credentials.builderPublicKey!);
  url.searchParams.set("userId", credentials.userId!);
  const res = await safeFetch(url, {
    method: "POST",
    body: JSON.stringify(body),
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${credentials.builderPrivateKey!}`,
    },
  });
  if (!res.ok) {
    throw new Error("Failed to generate completion:\n" + (await res.text()));
  }
  const stream = transformStream(res.body);
  for await (const value of stream) {
    if (value.length === 0) {
      continue;
    }
    const json = JSON.parse(value) as ArtifactItem;
    yield json;
  }
}

async function completionFeedback(
  sys: DevToolsSys,
  credentials: Credentials,
  id: string,
  feedbackSentiment?: "positive" | "negative",
  feedbackText?: string
) {
  try {
    const url = new URL(`${apiAirV2Host}/codegen/feedback`);
    url.searchParams.set("apiKey", credentials.builderPublicKey!);
    url.searchParams.set("userId", credentials.userId!);
    await safeFetch(url, {
      method: "POST",
      body: JSON.stringify({
        id: id,
        feedbackText,
        feedbackSentiment: feedbackSentiment,
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${credentials.builderPrivateKey!}`,
      },
    });
  } catch (error) {
    sys.Sentry?.captureException(error);
  }
}
async function searchCodeBase(
  userId: string,
  spaceId: string,
  privateKey: string,
  body: CodeGenSearchBody
) {
  const url = new URL(`${apiAirV2Host}/codegen/search`);
  url.searchParams.set("apiKey", spaceId!);
  url.searchParams.set("userId", userId!);
  const res = await safeFetch(url, {
    method: "POST",
    body: JSON.stringify(body),
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${privateKey!}`,
    },
  });
  if (!res.ok) {
    throw new Error("Failed to search codebase:\n" + (await res.text()));
  }
  const results = (await res.json()) as RankedResult[];
  const setFiles = new Set<string>();
  for (const result of results) {
    setFiles.add(result.filePath);
  }
  return Array.from(setFiles);
}

export async function createApp(
  userId: string,
  spaceId: string,
  privateKey: string,
  body: any
) {
  const url = new URL(`${apiAirV2Host}/codegen/create-app`);
  url.searchParams.set("apiKey", spaceId!);
  url.searchParams.set("userId", userId!);
  const res = await safeFetch(url, {
    method: "POST",
    body: JSON.stringify(body),
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${privateKey!}`,
    },
  });
  if (!res.ok) {
    throw new Error("Failed to create app:\n" + (await res.text()));
  }
  const results = await res.json();
  console.log("results", results);
  return;
}

function shouldIncludeHiddenFile(sys: DevToolsSys, file: string) {
  if (file.includes(".test.")) {
    return false;
  }
  if (file.includes(".spec.")) {
    return false;
  }
  if (file.includes(".unit.")) {
    return false;
  }
  if (file.includes(".e2e.")) {
    return false;
  }
  if (file.includes("test")) {
    return false;
  }
  if (file.includes("tests")) {
    return false;
  }
  if (sys.basename(file).startsWith(".")) {
    return false;
  }
  return true;
}

function shouldIncludeFile(
  file: string,
  foundFiles: string[],
  selectedFilePaths: Map<string, Importance>,
  fallbackImportance: Importance | 0
): Importance | 0 {
  const defaultImportance = selectedFilePaths.get(file) ?? 0;
  if (file.endsWith("package.json")) {
    return 3;
  }
  if (file.endsWith("tsconfig.json")) {
    return 3;
  }
  if (file.includes("tailwind.config")) {
    return 3;
  }
  if (file.includes("next.config")) {
    return 3;
  }
  if (file.endsWith("components.json")) {
    return 3;
  }
  if (file.endsWith("/app.component.ts")) {
    return 3;
  }
  if (file.endsWith("/app.component.html")) {
    return 3;
  }
  if (file.endsWith("/app.component.css")) {
    return 3;
  }
  if (file.endsWith("/app.component.scss")) {
    return 3;
  }
  if (file.endsWith("/app.module.ts")) {
    return 3;
  }
  if (file.endsWith("/app.config.ts")) {
    return 3;
  }
  if (file.endsWith("/app.routes.ts")) {
    return 3;
  }
  if (file.includes("components/ui/")) {
    return defaultImportance;
  }
  if (file.endsWith(".mapper.tsx") || file.endsWith(".mapper.jsx")) {
    return defaultImportance;
  }
  if (file.includes("vendor/")) {
    return defaultImportance;
  }
  if (file.startsWith("lib/")) {
    return defaultImportance;
  }
  if (file.endsWith("package-lock.json")) {
    return defaultImportance;
  }
  if (file.endsWith("builder-registry.ts")) {
    return defaultImportance;
  }

  if (file.includes("layout")) {
    return Math.max(defaultImportance, 2) as Importance;
  }

  if (countSlashes(file) < 3) {
    if (file.includes("route")) {
      return Math.max(defaultImportance, 3) as Importance;
    }
  }

  if (countSlashes(file) < 2) {
    if (file.endsWith(".css") || file.endsWith(".scss")) {
      return Math.max(defaultImportance, 2) as Importance;
    }
  }

  if (foundFiles.length > 0) {
    if (foundFiles.some((a) => file.includes(a))) {
      return Math.max(defaultImportance, 2) as Importance;
    }
  } else {
    if (file.includes("components/ui/")) {
      return defaultImportance;
    }
  }

  // not include if not code file (.ts, .tsx, .js, .jsx, .md, .html)
  if (
    !file.endsWith(".ts") &&
    !file.endsWith(".tsx") &&
    !file.endsWith(".js") &&
    !file.endsWith(".jsx") &&
    !file.endsWith(".html") &&
    !file.endsWith(".html.erb")
  ) {
    return defaultImportance;
  }
  return Math.max(defaultImportance, fallbackImportance) as Importance;
}

function countSlashes(str: string): number {
  return (str.match(/\//g) || []).length;
}

export async function* transformStream(
  body: ReadableStream<Uint8Array> | null
) {
  if (!body) {
    return;
  }
  let accumulatedValue = "";
  const reader = body.getReader();
  let done, value;
  while (!done) {
    ({ value, done } = await reader.read());
    if (done) {
      break;
    } else {
      const str = new TextDecoder().decode(value);
      accumulatedValue += str;
      const parts = accumulatedValue.split("\n");
      for (let i = 0; i < parts.length - 1; i++) {
        yield parts[i];
      }
      accumulatedValue = parts[parts.length - 1];
    }
  }
}

function runShell(cmd: string, options?: SpawnOptions) {
  let child: ChildProcess;

  const command = new Promise<boolean>((resolve) => {
    try {
      child = spawn(cmd, {
        shell: true,
        ...options,
      });

      child.on("error", () => {
        resolve(false);
      });

      child.on("close", (code) => {
        if (code === 0) {
          resolve(true);
        } else {
          resolve(false);
        }
      });
    } catch (e: any) {
      resolve(false);
    }
  });

  return command;
}

async function getCustomInstructions(sys: DevToolsSys, basePath: string) {
  const customInstructions: CustomInstruction[] = [];
  async function tryAddCustomInstructions(path: string) {
    try {
      const ignorePath = sys.resolve(basePath, path);
      if (await sys.exists(ignorePath)) {
        const customInstructionsContent = await sys.readFile(ignorePath);
        if (customInstructionsContent) {
          const id = "hash-" + (await sys.hash(customInstructionsContent));
          customInstructions.push({
            id: id,
            name: path,
            content: customInstructionsContent,
          });
        }
      }
    } catch (error) {
      console.debug(`No ${path} found or error reading it:`, error);
    }
  }
  await tryAddCustomInstructions(".cursorrules");
  await tryAddCustomInstructions(".builderrules");
  return customInstructions;
}

function getFeedbackFormUrl(
  sessionId: string,
  spaceId: string | undefined
): string {
  const FORM_SHORT_URL =
    "https://docs.google.com/forms/d/e/1FAIpQLSdHUIxeNZCAeMTgAWehxBtZplgcqXqyPbGf9Z1Advnf2ARxGw/viewform";
  const SESSION_ID_PARAM = "entry.986969459";
  const SPACE_ID_PARAM = "entry.1867678972";
  const url = new URL(FORM_SHORT_URL);
  url.searchParams.set("usp", "pp_url");
  url.searchParams.set(SESSION_ID_PARAM, sessionId);
  if (spaceId) {
    url.searchParams.set(SPACE_ID_PARAM, spaceId);
  }
  return url.toString();
}

function printFeedbackText(sessionId: string, spaceId: string | undefined) {
  const feedbackUrl = getFeedbackFormUrl(sessionId, spaceId);
  console.log(
    `\n   📝 We'd love your feedback on the code generation!\n   ${dim(
      feedbackUrl
    )}\n`
  );
}
/**
 * Format usage information as a progress bar
 * Usage: 1,234/5,000
 * █████░░░░░░░░░░░░░░░
 */
function formatUsage(total: number, limit: number): string {
  // Calculate percentage
  const percentage = Math.round((total / limit) * 100);

  // Use half of terminal width for the progress bar
  const terminalWidth = process.stdout.columns || 80;
  const barLength = Math.floor(terminalWidth / 2);
  const filledLength = Math.round((percentage / 100) * barLength);
  const emptyLength = barLength - filledLength;

  const filled = "█".repeat(filledLength);
  const empty = "░".repeat(emptyLength);

  // Format numbers with commas
  const formattedTotal = total.toLocaleString();
  const formattedLimit = limit.toLocaleString();

  return `Usage: ${formattedTotal}/${formattedLimit}
${filled}${empty}`;
}

