import type { CreateDevToolsOptions, DevTools } from "../types";
import { detectFrameworks, detectTurbopack } from "./detect-frameworks";
import { createNextDevTools } from "./adapters/next";
import { createQwikCityDevTools } from "./adapters/qwik-city";
import { createRemixDevTools } from "./adapters/remix";
import { createReactDevTools } from "./adapters/react";
import { createAngularDevTools } from "./adapters/angular";
import { exportRegistry, importRegistry } from "./import-export-registry";
import { SUPPORTED_FRAMEWORKS } from "../common/builder/builder-sdks";
import { findAllDependencies } from "./find-dependencies";

const FRAMEWORK_ADAPTERS: { [name: string]: typeof createQwikCityDevTools } = {
  "@builder.io/qwik-city": createQwikCityDevTools,
  next: createNextDevTools,
  "@remix-run/react": createRemixDevTools,
  react: createReactDevTools,
  "@angular/core": createAngularDevTools,
};

const FRAMEWORK_FRIENDLY_NAMES: { [name: string]: string } = {
  "@builder.io/qwik-city": SUPPORTED_FRAMEWORKS.qwik,
  next: SUPPORTED_FRAMEWORKS.next,
  "@remix-run/react": SUPPORTED_FRAMEWORKS.remix,
  react: SUPPORTED_FRAMEWORKS.react,
  "@angular/core": SUPPORTED_FRAMEWORKS.angular,
};

export async function createDevTools(
  opts: CreateDevToolsOptions
): Promise<DevTools> {
  if (!opts.ts) {
    throw new Error(
      "createDevTools() requires a TypeScript instance passed to the 'ts' option"
    );
  }

  if (!opts.frameworks || opts.frameworks.length === 0) {
    opts.frameworks = await detectFrameworks(opts);
  }
  if (opts.frameworks.length === 0) {
    throw new Error(`Supported Devtools framework for not found`);
  }

  for (const framework of opts.frameworks) {
    const createDevTools = FRAMEWORK_ADAPTERS[framework.name];
    if (createDevTools) {
      if (framework.name === "next") {
        const isTurbopack = await detectTurbopack(opts);
        if (isTurbopack) {
          throw new Error(
            `Turbopack does not support the Builder.io devtools cli. Please disable Turbopack or manually integrate Builder.io into your Next.js project.`
          );
        }
      }
      const devToolsAdapter = await createDevTools(opts);
      const devTools: DevTools = {
        ...devToolsAdapter,
        exportRegistry: () => {
          return exportRegistry(opts, devToolsAdapter);
        },
        importRegistry: (exportedRegistry) => {
          return importRegistry(opts, devToolsAdapter, exportedRegistry);
        },
        framework: FRAMEWORK_FRIENDLY_NAMES[framework.name],
        findAllDependencies: async () => {
          return await findAllDependencies(opts, opts.getRepoRootDir());
        },
      };
      return devTools;
    }
  }

  throw new Error(
    `Devtools framework not found for ${opts.getAppRootDir()}, ${JSON.stringify(
      opts.frameworks,
      null,
      2
    )}`
  );
}
