import { createDevTools, type DevToolsSys } from "@builder.io/dev-tools/core";
import { createDevToolsNodeSys } from "@builder.io/dev-tools/node";
import { createDevToolsServer } from "@builder.io/dev-tools/server";
import mri from "mri";
import { runFigmaCommand } from "./figma";
import * as Amplitude from "@amplitude/analytics-node";
import { cancel } from "@clack/prompts";
import { addCommand } from "./builder-add/interface";
import { getOptions } from "./builder-add/options";
import { setupSyncServer, syncCommand } from "./sync-utils";
import { exit } from "./exit";
import { initTracking } from "./track";
import * as Sentry from "@sentry/node";
import { printHelp } from "./help";
import { runCodeCommand } from "./code";

export interface CLIArgs {
  /** Builder.io private API key */
  privateKey?: string;
  /** Figma access token */
  figmaToken?: string;
  /** Run in CI mode without interactive prompts */
  ci?: boolean;
  /** Show help text */
  help?: boolean;
  /** Force operation even if there are warnings */
  force?: boolean;
  /** Print output as JSON */
  printJson?: boolean;
  /** Run in dry-run mode without making changes */
  dryrun?: boolean;
  /** Auto-confirm all prompts */
  yes?: boolean;
  /** Builder.io space ID */
  spaceId?: string;
  /** Skip package installation step */
  skipInstallation?: boolean;
  /** Url to start from */
  url?: string;
  /** Prompt text for non-interactive mode */
  prompt?: string;
  /** Generation mode - either 'exact' for precise matches or 'creative' for more flexibility */
  mode?: "exact" | "creative";
  /** Working directory to run commands from */
  cwd?: string;
  /** Raw command line arguments */
  _: string[];
}

async function setupDevTools(sys: DevToolsSys) {
  sys.debug("builder dev tools cli init");
  const devTools = await createDevTools(sys);

  await createDevToolsServer({
    ...devTools,

    getClientId: () => "builder-dev-tools-cli",

    closeAppServer: async () => {},

    restartAppServer: async () => {
      sys.debug("restart server");
    },

    enableAppWatch: async (enable) => {
      return enable;
    },

    ...sys,
  });
}
const handleSigTerm = async () => {
  await Amplitude.flush().promise;
  process.exit(0);
};

process.on("SIGINT", handleSigTerm);
process.on("SIGTERM", handleSigTerm);

async function main() {
  await initTracking();

  try {
    const args = mri<CLIArgs>(process.argv.slice(2), {
      string: [
        "figmaToken",
        "privateKey",
        "spaceId",
        "cwd",
        "prompt",
        "mode",
        "url",
      ],
      boolean: [
        "help",
        "ci",
        "force",
        "printJson",
        "dryrun",
        "yes",
        "skipInstallation",
      ],
    });

    if (args.help) {
      printHelp(args._[args._.length - 1]);
      return await exit(0);
    }

    const rootCommand = args._[0];
    if (args.cwd) {
      console.log("changing cwd", args.cwd);
      process.chdir(args.cwd);
    }
    const sys = await createDevToolsNodeSys({
      cwd: process.cwd(),
      ignoreMissingConfig: true,
    });
    if (rootCommand === "figma") {
      const subCommand = args._[1];
      try {
        await runFigmaCommand(sys, subCommand, args);
      } catch (error) {
        Sentry.captureException(error);
        console.error(error);
        return await exit(1);
      }
    } else if (rootCommand === "code") {
      const subCommand = args._[1];
      try {
        await runCodeCommand(sys, subCommand, args);
      } catch (error) {
        Sentry.captureException(error);
        console.error(error);
        return await exit(1);
      }
    } else if (rootCommand === "add") {
      const opts = getOptions(sys.getAppRootDir(), process.argv.slice(2));
      await addCommand(opts);
    } else if (rootCommand === "sync") {
      const opts = getOptions(sys.getAppRootDir(), process.argv.slice(2));
      const initialSyncInfo = await syncCommand(opts);
      setupSyncServer(sys, initialSyncInfo)
        .then(() => {
          console.info("Builder dev tools is running");
        })
        .catch((error) => {
          Sentry.captureException(error);
          console.error("Cannot start builder dev tools", error);
        });
    } else if (rootCommand === "help") {
      printHelp(args._[1]);
      return await exit(0);
    } else if (
      rootCommand === "publish" ||
      rootCommand === "migrate" ||
      rootCommand === "generate"
    ) {
      cancel(
        "Command not found, do you mean `npx builder.io figma " +
          rootCommand +
          "`?"
      );
      return await exit(1);
    } else {
      // Using cli to run dev tools dev server. This is used in angular
      setupDevTools(sys)
        .then(() => {
          console.info("Builder dev tools is running");
        })
        .catch((error) => {
          Sentry.captureException(error);
          console.error("Cannot start builder dev tools", error);
        });
    }
  } catch (error) {
    Sentry.captureException(error);
    console.error(error);
    return await exit(1);
  }
}

main();
