import * as Amplitude from "@amplitude/analytics-node";
import * as Sentry from "@sentry/node";
import { version } from "../package.json";
import {
  configHasProperty,
  readConfigFile,
  writeConfigFile,
} from "../node/node-sys";
import { v4 as uuidv4 } from "uuid";
import os from "os";

export async function initTracking() {
  // ID used to group Amplitude events together
  // always a chance of collision but unlikely and not the end of the world
  // if we went duplicate events to the same session
  const trackingEnabled =
    SENTRY_ENABLED === "true" && process.env.NODE_ENV !== "test";
  let deviceId = await getDeviceId();
  let userId = await getUserId();

  Amplitude.init(trackingEnabled ? "2532be1b0436a18cb938b21fc7fa9faf" : "", {
    logLevel: Amplitude.Types.LogLevel.None,
    flushIntervalMillis: 500,
  });

  const identifyObj = new Amplitude.Identify();
  await Amplitude.identify(identifyObj, {
    device_id: deviceId,
    user_id: userId,
    platform: os.type(),
    os_name: process.platform,
    os_version: os.release(),
  }).promise;

  Sentry.init({
    dsn: "https://3252dddc08dbb5bf406dc33bf3c13633@o117565.ingest.us.sentry.io/4505630794448896",
    tracesSampleRate: 1.0,
    release: version,
    environment: process.env.NODE_ENV,
    enabled: trackingEnabled,
    integrations: [
      new Sentry.Integrations.Http({ tracing: true }),
      ...Sentry.autoDiscoverNodePerformanceMonitoringIntegrations(),
    ],
  });

  Sentry.setUser({
    username: process.env.USER || process.env.USERNAME || "unknown",
  });
}

export async function track(
  eventName: string,
  options: Record<string, any>,
  eventOptions?: Amplitude.Types.EventOptions
) {
  let props: Record<string, any> = {
    ...options,
    app: "dev-tools-cli",
    package: "cli",
  };

  return Amplitude.track(eventName, props, {
    ...eventOptions,
    user_id: await getUserId(),
    device_id: await getDeviceId(),
  });
}

let userId: string | undefined = undefined;
let deviceId: string | undefined = undefined;

export async function setUserId(id: string) {
  userId = id;
  const devToolsConfig = await readConfigFile();
  await writeConfigFile({ ...devToolsConfig, userId: id });
}

async function getUserId(): Promise<string> {
  if (userId) {
    return userId!;
  }
  const devToolsConfig = await readConfigFile();
  let uniqueTrackingId;
  if (configHasProperty(devToolsConfig, "userId")) {
    uniqueTrackingId = devToolsConfig.userId;
  }
  userId = uniqueTrackingId;
  return uniqueTrackingId;
}

async function getDeviceId(): Promise<string> {
  if (deviceId) {
    return deviceId!;
  }
  const devToolsConfig = await readConfigFile();
  let uniqueTrackingId = uuidv4();
  if (configHasProperty(devToolsConfig, "deviceId")) {
    uniqueTrackingId = devToolsConfig.deviceId;
  } else {
    await writeConfigFile({ ...devToolsConfig, deviceId: uniqueTrackingId });
  }
  deviceId = uniqueTrackingId;
  return uniqueTrackingId;
}

declare const SENTRY_ENABLED: string;
