import * as Sentry from "@sentry/node";
import { safeFetch } from "./fetch";

interface KVGetRequest {
  privateKey: string;
  spaceId: string;
  docId: string;
}

interface KVPutRequest {
  privateKey: string;
  spaceId: string;
  docId: string;
  /**
   * Must be an object but not an array. TS is a fool sometimes.
   * https://stackoverflow.com/a/70448528/1520787
   */
  data: Record<string, any> & { length?: never };
}

const apiHost = "https://cdn.builder.io";

export async function putBuilderSpaceKv(kvReq: KVPutRequest) {
  try {
    const rsp = await safeFetch(
      `${apiHost}/api/internal/kv/${kvReq.spaceId}/${kvReq.docId}?apiKey=${kvReq.spaceId}`,
      {
        method: "PUT",
        headers: {
          Authorization: `Bearer ${kvReq.privateKey}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(kvReq.data),
      }
    );

    if (!rsp.ok) {
      throw new Error(`HTTP ${rsp.status} ${rsp.statusText}`);
    }

    return rsp.json();
  } catch (error) {
    Sentry.captureException(error);
    console.error("PUT failed", error);
    return;
  }
}

export async function getBuilderSpaceKv(kvReq: KVGetRequest) {
  try {
    const rsp = await safeFetch(
      `${apiHost}/api/internal/kv/${kvReq.spaceId}/${kvReq.docId}?apiKey=${kvReq.spaceId}`,
      {
        headers: {
          Authorization: `Bearer ${kvReq.privateKey}`,
        },
      }
    );

    if (rsp.status === 404) {
      Sentry.captureMessage("getBuilderSpaceKv 404");
      console.warn("GET 404", rsp.url);
      return null;
    }

    if (!rsp.ok) {
      throw new Error(`HTTP ${rsp.status} ${rsp.statusText}`);
    }

    return rsp.json();
  } catch (error) {
    Sentry.captureException(error);
    console.error("GET failed", error);
    return;
  }
}
